import React, { useContext, useEffect, useState } from "react";

import { CrearAdmin } from "./crearAdmin";
import { CrearGaleria } from "./crearGaleria";
import { Login } from "../login";

import axios from "axios";
import { Context } from "../../contextos/Contexto";

export function InicializarPagina(){
    const { root , qUsuarios, qGalerias} = useContext(Context);

    console.log(qUsuarios, qGalerias);

    useEffect(() => {
        async function qGalUs(){
            let qUs = await (await axios.post(`${root}/countUsuarios`)).data;
            let qGal = await (await axios.post(`${root}/countGalerias`)).data;
            
            console.log(qUs);

            qUs >= 1 && qGal >= 1 ? window.location.href = `${root}/login` : console.log(true);
    
            return true;
        }

        qGalUs();
    }, []);

    if(qUsuarios === 0){
        return <CrearAdmin />
    } else if(qUsuarios > 0 && qGalerias === 0){
        return <CrearGaleria />
    } else {
        return <>
            {/*<h1 style={{ fontWeight:'bolder' }}>404 NOT FOUND</h1>*/}
        </>;
    }
}