import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function Status(){
    return <>
        <Form.Group as={Col}>
            <Form.Label>Status</Form.Label>
            <Form.Select id="status" placeholder='Status'>
                <option value={'disponible'}>Disponible</option>
                <option value={'enProceso'}>En proceso</option>
                <option value={'agotada'}>Agotada</option>
                <option value={'sobrePedido'}>Sobre pedido</option>
            </Form.Select>
        </Form.Group>
    </>
}

export function status(){
    const status = document.querySelector(`#status`).value;
    return status;
}

export function verStatus(value){
    document.querySelector(`#status`).value = value;
    return document.querySelector(`#status`).value;
}