import React, { useContext, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";

import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";
import { RichTextEditor } from "./jodit";
import { ObraImg, verObraImg, obraImg } from "./_obraImg";
import { Titulo, verTitulo, titulo } from "./_titulo";
import { Tecnica, verTecnica, tecnica } from "./_tecnica";
import { Medidas, verMedidas, medidas } from "./_medidas";
import { Precio, verPrecio, precio } from "./_precio";
import { CantidadDisponible, verCantidadDisponible, cantidadDisponible } from "./_cantidadDisponible";
import { Status, verStatus, status } from "./_status";

import { SellerName, verSellerName, sellerName } from "./_sellerName";
import { StageName, verStageName, stageName } from "./_stageName";
import { HtmlDescription, verHtmlDescription, htmlDescription } from "./_htmlDescription";

import { Context } from "../../contextos/Contexto";
import axios from "axios";

export function CrearObra(){
    const { Artistas, root } = useContext(Context);
    const { autorId } = useParams();
    const navigate = useNavigate();

    console.log('ID', autorId);

    useEffect(llenarDatos);

    function validarDatos(){
        const resultado = { 
            productImg: obraImg(), 
            fichaTecnica: {
                autor:stageName(),
                titulo:titulo(),
                ancho:medidas().ancho,
                alto:medidas().alto,
                tecnica:tecnica(),
                descripcion:htmlDescription()
            },
            precio:precio(),
            cantidad:cantidadDisponible(),
            status:status()
        }
        console.log(resultado);

        return resultado;
    }

    function llenarDatos(){
        try{
            let Artista = Artistas.find(Art => Art._id == autorId);
            console.log(Artista);
            verStageName(Artista.stageName);

        } catch(err){
            console.log('Hubo un error', err);
        }
    }

    async function crearObra(){
        let datos = validarDatos();
            datos.autorId = autorId;
        var response = await axios.post(`${root}/crearProduct`, datos);
        console.log('creandoObra', response, datos);

        navigate(`/admin/obras/${response.data._id}`);
        window.location.reload();
        return true;
    }

    return <>
        <Form className="
                border 
                border-primary  
                py-3 
                px-3"
        >

            <ObraImg />

            <h4>Autor</h4>

                <StageName />
                
                <Titulo />

                <Tecnica />
            
                <Medidas />

                <Row className="mb-3">
                    <Precio />
                    
                    <CantidadDisponible />

                    <Status />
                </Row>

                <HtmlDescription />
            
            <Button variant='primary' className="mx-auto" style={ { width:'30%', display:'block' } } onClick={ crearObra }> Crear Obra </Button>
        </Form>


    </>
}