import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function WhatsApp(){
    return <>
        <Form.Group as={Col}>
            <Form.Label>Numero de WhatsApp</Form.Label>
            <Form.Control className="whatsApp" type='number' placeholder='Cel' />
        </Form.Group>
    </>
}

export function whatsApp(){
    const whatsApp = [];
    document.querySelectorAll(`.whatsApp`).forEach((El, i) => { whatsApp.push(El.value); });
    return whatsApp;
}

export function verWhatsApp(values){
    const _whatsApp = whatsApp;

    values.forEach((Val, i) => {
        document.querySelectorAll(`.whatsApp`)[i].value = Val;
    });
    return _whatsApp();
}