import React, { useContext, useEffect } from "react";
import { useParams } from "react-router-dom";

import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";
import { RichTextEditor } from "./jodit";
import { ObraImg, verObraImg, obraImg } from "./_obraImg";
import { Titulo, verTitulo, titulo } from "./_titulo";
import { Tecnica, verTecnica, tecnica } from "./_tecnica";
import { Medidas, verMedidas, medidas } from "./_medidas";
import { Precio, verPrecio, precio } from "./_precio";
import { CantidadDisponible, verCantidadDisponible, cantidadDisponible } from "./_cantidadDisponible";
import { Status, verStatus, status } from "./_status";

import { SellerName, verSellerName, sellerName } from "./_sellerName";
import { StageName, verStageName, stageName } from "./_stageName";
import { HtmlDescription, verHtmlDescription, htmlDescription } from "./_htmlDescription";

import { Context } from "../../contextos/Contexto";
import axios from "axios";

export function EditarObras(){
    const { Products, root } = useContext(Context);
    const { id } = useParams();
    var Obra = Products.find((Prod) => Prod._id == id);

    console.log('ID', id);

    useEffect(llenarDatos);

    function validarDatos(){
        const resultado = { 
            productImg: obraImg(), 
            fichaTecnica: {
                autor:stageName(),
                titulo:titulo(),
                ancho:medidas().ancho,
                alto:medidas().alto,
                tecnica:tecnica(),
                descripcion:htmlDescription()
            },
            precio:precio(),
            cantidad:cantidadDisponible(),
            status:status()
        }
        console.log(resultado);

        return resultado;
    }

    function llenarDatos(){
        try{
            if(!Obra) return ; //El artista no existe o los datos no se han cargado
            const { productImg, fichaTecnica, precio, cantidad, status, autorId  } = Obra;
            const { autor, titulo, ancho, alto, tecnica, descripcion } = fichaTecnica;
            console.log(autor);

            verPrecio(precio);
            verCantidadDisponible(cantidad);
            verStatus('sobrePedido');

            verStageName(autor);
            verTitulo(titulo);
            verTecnica(tecnica);
            verMedidas({ ancho, alto });
            verHtmlDescription(descripcion);
            verStatus(status);
            verObraImg(productImg);

        } catch(err){
            console.log('Hubo un error', err);
        }
    }

    async function actualizarObra(){
        console.log('actualizandoObra');
        let datos = validarDatos();
        let response = await axios.post(`${root}/actualizarProduct`, { datos, id });
        console.log(response);

        window.location.reload()
        return response;
    }

    async function borrarObra(){
        console.log('borrandoObra');
    }

    return <>
        <Form className="
                border 
                border-primary  
                py-3 
                px-3"
        >

            <ObraImg />

            <h4>Autor</h4>

                <StageName placeholder="Nombre Artistico del autor"/>
                
                <Titulo />

                <Tecnica />
            
                <Medidas />

                <Row className="mb-3">
                    <Precio />
                    
                    <CantidadDisponible />

                    <Status />
                </Row>

                <HtmlDescription />
            
            <Button variant='primary' className="mx-auto" style={ { width:'30%', display:'block' } } onClick={ actualizarObra }> Actualizar Obra </Button>
        </Form>


    </>
}