import React, { useContext, useEffect } from "react";
import { useParams } from "react-router-dom";

import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";
import { RichTextEditor } from "./jodit";

import { SellerName, verSellerName, sellerName } from "./_sellerName";
import { StageName, verStageName, stageName } from "./_stageName";
import { Locations, verLocations, locations } from "./_locations";
import { WhatsApp, verWhatsApp, whatsApp} from "./_whatsApp"
import { SocialNetworks, verSocialNetworks, socialNetworks } from "./_socialNetworks";
import { Emails, verEmails, emails } from "./_emails";
import  { HtmlDescription, verHtmlDescription, htmlDescription } from "./_htmlDescription";

import { Context } from "../../contextos/Contexto";
import axios from "axios";
import { galeriaImg, GaleriaImg, verGaleriaImg } from "./_galeriaImg";

export function EditarGalerias(){
    var { Galeria, root } = useContext(Context);
    var galeriaId = '';
    
    try{ galeriaId = Galeria[0]._id; } catch(err) { console.log() }

    useEffect(llenarDatos);

    function validarDatos(){
        const resultado = {  
            owner: sellerName(), 
            galeryName: stageName(),
            locations: locations(), 
            whatsApp: whatsApp(), 
            socialNetworks: socialNetworks(), 
            emails: emails(),
            logoCDN: galeriaImg()
            //workIds: workIds()
        }
        console.log(resultado);

        return resultado;
    }

    function llenarDatos(){
        try{
            const { owner, galeryName, locations, whatsApp, 
                    socialNetworks, emails, logoCDN } = Galeria[0];

            console.log(Galeria);

            verSellerName(owner);
            verStageName(galeryName);
            verLocations(locations);
            verWhatsApp(whatsApp);
            verSocialNetworks(socialNetworks);
            verEmails(emails);
            verGaleriaImg(logoCDN);

        } catch(err){
            console.log();
        }
    }

    async function actualizarGaleria(){
        let datos = validarDatos();
        console.log(datos);
        let response = await axios.post(`${root}/actualizarGaleria`, { datos, galeriaId });
        console.log(response);
        
        window.location.reload()
        return response;
    }

    try { 
        return <>
            <Form className="
                    border 
                    border-primary  
                    py-3 
                    px-3"
            >
                    <GaleriaImg />

                    <h4>Propietario</h4>
                    <SellerName /> { /* Nombre del dueño */ }

                    <StageName placeholder={'Nombre de la Galería'}/>{/* Nombre de la Galeria */}

                    <Row className="mb-3">   
                        <Emails />
                        <WhatsApp />
                    </Row>
                    
                    <SocialNetworks socialNetsArr={Galeria[0].socialNetworks}/>
                    
                    <Locations />
                
                <Button variant='primary' className="mx-auto" style={ { width:'30%', display:'block' } } onClick={actualizarGaleria}> Actualizar Galeria </Button>
            </Form>


        </>;
    } catch(err){ 
        console.log('err')
        return <></>
    }
}