import React, { useEffect, useState, createContext } from "react";
import axios from "axios";

import dataArtista from '../data/Artista.json';
import dataComprador from '../data/Comprador.json';
import dataProducts from '../data/Products.json';
import dataPropietario from '../data/Propietario.json';
import dataUsuario from '../data/Usuario.json';

export const Context =  createContext();

export function ContextProvider(props){
    
    const root = window.location.origin;

    useEffect(() => {
        const traerDatos = async function(){
            let arts = await axios.post(`${root}/verArtistas`);
            let prods = await axios.post(`${root}/verProducts`);
            let gal = await axios.post(`${root}/verGalerias`);
            let qUs = await axios.post(`${root}/countUsuarios`);
            let qGal = await axios.post(`${root}/countGalerias`);
            //let us = await axios.post(`${root}/verUsuario`);  ???

            setArtistas(arts.data);
            setProducts(prods.data);
            setGaleria(gal.data);

            setQUsuarios(qUs.data);
            setQGalerias(qGal.data);
        }

        traerDatos();
    },[]);

    var [vista, setVista] =  useState('login');
    
    var [ Artistas, setArtistas ] = useState([]);
    var [ Products, setProducts ] = useState([]);
    var [ Galeria, setGaleria ] = useState({});
    var [ Usuario, setUsuario ] = useState({});

    var [ qUsuarios, setQUsuarios ] = useState(-1);
    var [ qGalerias, setQGalerias ] = useState(-1);

    return (<Context.Provider value={{
        Artistas,
        Products,
        Galeria,
        Usuario,
        root,

        qUsuarios,
        qGalerias,

        dataArtista,
        dataComprador,
        dataProducts,
        dataPropietario,
        dataUsuario,

        vista,
        
        setVista,
        setArtistas,
        setProducts,
        setGaleria,
        setUsuario
    }}>
        {props.children}
    </Context.Provider>);
}