import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function SellerName(){
    return <>
        <Row className="mb-3">    
            <Form.Group as={Col}>
                <Form.Label>Nombre(s)</Form.Label>
                <Form.Control id="name" type='text' placeholder='Nombre(s)'/>
                <Form.Text className='text-muted'>
                    No compartiremos esta información con nadie
                </Form.Text>
            </Form.Group>

            <Form.Group as={Col}>
                <Form.Label>Apellidos</Form.Label>
                <Form.Control id="lastName" type='text' placeholder='Apellidos' />
                <Form.Text className='text-muted'>
                    No compartiremos esta información con nadie
                </Form.Text>
            </Form.Group>
        </Row>
    </>
}

export function sellerName(){
    //SellerName
    const name = document.querySelector(`#name`).value;
    const lastName = document.querySelector(`#lastName`).value;
    const sellerName = { name, lastName };

    return sellerName;
}

export function verSellerName(value = { name:"", lastName:"" }){
    document.querySelector(`#name`).value = value.name;
    document.querySelector(`#lastName`).value = value.lastName;

    //SellerName
    const name = document.querySelector(`#name`).value;
    const lastName = document.querySelector(`#lastName`).value;
    const sellerName = { name, lastName };

    return sellerName;
}