import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function Locations(){
    return <>
        <h4>Dirección</h4>
        <Row className="mb-3">   
            <Form.Group as={Col}>
                <Form.Label>Calle</Form.Label>
                <Form.Control className="calle" type='text' placeholder='Av. 16 de Septiembre' />
            </Form.Group> 
            <Form.Group as={Col}>
                <Form.Label>Numero</Form.Label>
                <Form.Control className="numero" type='number' placeholder='123' />
            </Form.Group>
            <Form.Group as={Col}>
                <Form.Label>Extension</Form.Label>
                <Form.Control className="extension" type='string' placeholder='A' />
            </Form.Group>
            <Form.Group as={Col}>
                <Form.Label>Código Postal</Form.Label>
                <Form.Control className="cp" type='number' placeholder='44100' />
            </Form.Group>
        </Row>

        <Row className="mb-3">   
            <Form.Group as={Col}>
                <Form.Label>Colonia</Form.Label>
                <Form.Control className="colonia" type='text' placeholder='Americana' />
            </Form.Group> 
            <Form.Group as={Col}>
                <Form.Label>Municipio</Form.Label>
                <Form.Control className="municipio" type='text' placeholder='Guadalajara' />
            </Form.Group>
            <Form.Group as={Col}>
                <Form.Label>Estado</Form.Label>
                <Form.Control className="estado" type='text' placeholder='Jalisco' />
            </Form.Group>
            <Form.Group>
                <Form.Label>Pais</Form.Label>
                <Form.Control className="pais" type='text' placeholder='México' readOnly/>
            </Form.Group>
        </Row>
    </>
}

export function locations(){
    const _locations = [];

    document.querySelectorAll(`.calle`).forEach((El, i) => {
        const location = {};
                location.calle = El.value;
                location.numero = document.querySelectorAll(`.numero`)[i].value;
                location.extension = document.querySelectorAll(`.extension`)[i].value;
                location.cp = document.querySelectorAll(`.cp`)[i].value;
                location.colonia = document.querySelectorAll(`.colonia`)[i].value;
                location.municipio = document.querySelectorAll(`.municipio`)[i].value;
                location.estado = document.querySelectorAll(`.estado`)[i].value;
                location.pais = document.querySelectorAll(`.pais`)[i].value;
            //VALIDAR QUE LOS DATOS SEAN CORRECTOS

        _locations.push(location);
    });

    return _locations;
}

export function verLocations(values){
    const _locations = locations;

    values.forEach((Val, i) => {
        let { calle, numero, extension, cp, colonia, municipio, estado, pais } = Val;
        document.querySelectorAll(`.calle`)[i].value = calle;
        document.querySelectorAll(`.numero`)[i].value = numero;
        document.querySelectorAll(`.extension`)[i].value = extension;
        document.querySelectorAll(`.cp`)[i].value = cp;
        document.querySelectorAll(`.colonia`)[i].value = colonia;
        document.querySelectorAll(`.municipio`)[i].value = municipio;
        document.querySelectorAll(`.estado`)[i].value = estado;
        document.querySelectorAll(`.pais`)[i].value = pais;
    });
    return _locations();
}