import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function CantidadDisponible(){
    return <>
        <Form.Group as={Col}>
            <Form.Label>Cantidad disponible</Form.Label>
            <Form.Control id="cantidadDisponible" type='number' placeholder='1' />
        </Form.Group>
    </>
}

export function cantidadDisponible(){
    const cantidadDisponible = document.querySelector(`#cantidadDisponible`).value;
    return cantidadDisponible;
}

export function verCantidadDisponible(value){
    document.querySelector(`#cantidadDisponible`).value = value;
    return document.querySelector(`#cantidadDisponible`).value;
}