import React, { useContext } from "react";
import { Aside } from "./aside";
import { Button, Col, Row } from "react-bootstrap";
import { Form, InputGroup } from "react-bootstrap";

import { Context } from "../contextos/Contexto";
import { MostrarImagenes } from "./mostrarImagenes";
import { SeleccionarImagen } from "./seleccionarImagen";

export function SubirImagen(){
    const { root } = useContext(Context);
    
    return <main className="row">
        <Aside />

        <div className="col-9">
            
            
            <Form className="mb-5"
                    action={`${root}/subirImagen`}
                    method="POST" 
                    encType="multipart/form-data"
            >
                <Row className="mb-2">
                    <Form.Label>Subir Imagen</Form.Label>
                    <InputGroup>                    
                        <Form.Control id="fileGaleriaImg" name="imagen" type="file"></Form.Control>
                    </InputGroup>
                </Row>
                <Button type="submit" className="w-25 mx-auto" style={{display:'block'}}>Subir Imagen</Button>
            </Form>

            <div className="row">
                <MostrarImagenes />
            </div>
        </div>
    </main>;
}