import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import 'bootstrap/dist/css/bootstrap.min.css';

import { Login } from './componentes/login';
import { EditarObras } from './componentes/formularios/editarObras';
import { RichTextEditor } from './componentes/formularios/jodit';
import { EditarArtistas } from './componentes/formularios/editarArtistas';
import { EditarGalrias } from './componentes/formularios/editarGalerias';
import { Header } from './componentes/header';
import { Main } from './componentes/main';

import { ContextProvider } from './contextos/Contexto';
import { InicializarPagina } from './componentes/formularios/inicializarPagina';
import { SubirImagen } from './componentes/subirImagen';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <>
        <ContextProvider>
            <Header />
            
            <BrowserRouter>
                <Routes>
                    <Route path='/admin/inicializarPagina' element={<InicializarPagina />}/>
                    <Route path='/admin/subirImagen' element={<SubirImagen />}/>

                    <Route path='/admin' element={ <Main view="artistas" /> }></Route>
                    <Route path='/admin/login' element={ <Login view="login" /> } />
                    <Route path='/admin/artistas' element={ <Main view="artistas" /> }></Route>
                    <Route path='/admin/obras' element={ <Main view="obras" /> }></Route>
                    <Route path='/admin/verContactos' element={ <Main view="verContactos" /> }></Route>
                    
                    <Route path='/admin/artistas/:id' element={ <Main view="editarArtistas" /> }></Route>
                    <Route path='/admin/obras/:id' element={ <Main view="editarObras" /> }></Route>

                    <Route path='/admin/galerias/editarGaleria' element={ <Main view="editarGalerias" /> }></Route>

                    <Route path='/admin/artistas/crearArtista' element={ <Main view="crearArtista" /> }></Route>
                    <Route path='/admin/obras/crearObra/:autorId' element={ <Main view="crearObra" /> }></Route>
                </Routes>
            </BrowserRouter>

        </ContextProvider>
    </>
);