import React, { useContext, useEffect } from "react";

import { Form, Button, Row } from "react-bootstrap";

import { SellerName, verSellerName, sellerName } from "./_sellerName";
import { StageName, verStageName, stageName } from "./_stageName";
import { Locations, verLocations, locations } from "./_locations";
import { WhatsApp, verWhatsApp, whatsApp} from "./_whatsApp"
import { SocialNetworks, verSocialNetworks, socialNetworks } from "./_socialNetworks";
import { Emails, verEmails, emails } from "./_emails";
import  { HtmlDescription } from "./_htmlDescription";

import { Context } from "../../contextos/Contexto";
import axios from "axios";
import { galeriaImg, GaleriaImg, verGaleriaImg } from "./_galeriaImg";
import { useNavigate } from "react-router-dom";

export function CrearGaleria(){
    const { root } = useContext(Context);
    const navigate = useNavigate();
    var galeriaId = '';
    
    function validarDatos(){
        const resultado = { 
            owner: sellerName(), 
            galeryName: stageName(),
            locations: locations(), 
            whatsApp: whatsApp(), 
            socialNetworks: socialNetworks(), 
            emails: emails(),
            logoCDN: galeriaImg()
            //workIds: workIds()
        }
        console.log(resultado);

        return resultado;
    }

    async function crearGaleria(){
        let datos = validarDatos();
        console.log(datos);
        let response = await axios.post(`${root}/crearGaleria`, { datos });
        console.log(response);

        navigate(`/admin`);
        window.location.reload();
        return response;
    }

    return <>
        <Form className="
                border 
                border-primary  
                py-3 
                px-3
                w-75
                mx-auto
                my-3
                rounded"
        >
                <h4>Ingresa los datos de tu galería</h4>
                
                <GaleriaImg />

                <h4>Propietario</h4>
                <SellerName /> { /* Nombre del dueño */ }

                <h4 className="mt-5" style={{display:'block', textAlign:"center"}}>Sobre la Galería</h4>

                <StageName placeholder="Nombre de la Galería"/>{/* Nombre de la Galeria */}

                <h4>Medios de contacto</h4>

                <Row className="mb-3">   
                    <Emails />
                    <WhatsApp />
                </Row>

                <SocialNetworks />

                <Locations />
            
            <Button variant='primary' className="mx-auto" style={ { width:'30%', display:'block' } } onClick={crearGaleria}> Crear Galería </Button>
        </Form>


    </>
}