import axios from "axios";
import React from "react";
import { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { Context } from "../contextos/Contexto";

export function Aside(){
    const { setVista, root } = useContext(Context);

    const navigate = useNavigate();

    function verObras(){ navigate(`/admin/obras`) }
    function verArtistas(){ navigate(`/admin/artistas`) }   

    function crearArtista(){ navigate(`/admin/artistas/crearArtista`) }
    function crearObra(){ navigate('/admin/obras/crearObra') }
    function editarGalerias(){ navigate('/admin/galerias/editarGaleria') }
    function verContactos(){ navigate('/admin/verContactos') }
    function subirImagen(){ navigate('/admin/subirImagen') }
    async function isAuth(){
        await axios.post(`${root}/isAuth`, {});
    }
    function cerrarSesion(){ navigate('/admin/logout'); window.location.reload() }


    return <>
        <div className=" p-3 bg-white col-3" style={{/*"width": "30%", float:"left", minHeight:'50vh',*/ borderRight:'1px solid #ccc'}}>
            <a className="d-flex align-items-center pb-3 mb-3 px-3 link-dark text-decoration-none border-bottom">
                <span className="fs-5 fw-semibold">Opciones</span>
            </a>
            <ul className="list-unstyled ps-0">

                <li className="mb-1">
                    <button onClick={verObras} className="btn btn-toggle d-inline-flex align-items-center rounded border-0 collapsed" data-bs-toggle="collapse" data-bs-target="#home-collapse" aria-expanded="false">
                        <span className="material-symbols-outlined mx-1"> visibility </span>
                        Obras
                    </button>
                </li>
                <li className="mb-1">
                    <button onClick={verArtistas} className="btn btn-toggle d-inline-flex align-items-center rounded border-0" data-bs-toggle="collapse" data-bs-target="#account-collapse" aria-expanded="true">
                        <span className="material-symbols-outlined mx-1"> visibility </span>
                        Artistas
                    </button>
                </li>
                    
                    <hr />

                <li className="mb-1">
                    <button onClick={crearArtista} className="btn btn-toggle d-inline-flex align-items-center rounded border-0 collapsed" data-bs-toggle="collapse" data-bs-target="#home-collapse" aria-expanded="false">
                        <span className="material-symbols-outlined mx-1"> add_box </span>
                        Nuevo Artista
                    </button>
                </li>

                    <hr />

                <li className="mb-1">
                    <button onClick={editarGalerias} className="btn btn-toggle d-inline-flex align-items-center rounded border-0 collapsed" data-bs-toggle="collapse" data-bs-target="#home-collapse" aria-expanded="false">
                        <span className="material-symbols-outlined mx-1"> edit_square </span>
                        Editar Galería
                    </button>
                </li>

                    <hr />
                
                <li className="mb-1">
                    <button  onClick={verContactos} className="btn btn-toggle d-inline-flex align-items-center rounded border-0 collapsed" data-bs-toggle="collapse" data-bs-target="#home-collapse" aria-expanded="false">
                        <span className="material-symbols-outlined mx-1"> alternate_email </span>
                        Contactos
                    </button>
                </li>

                    <hr />

                <li className="mb-1">
                    <button  onClick={subirImagen} className="btn btn-toggle d-inline-flex align-items-center rounded border-0 collapsed" data-bs-toggle="collapse" data-bs-target="#home-collapse" aria-expanded="false">
                        <span className="material-symbols-outlined mx-1"> upload </span>
                        Subir Imagen
                    </button>
                </li>

                    <hr />
                                
                <li className="mb-1">
                    <button onClick={cerrarSesion} className="btn btn-toggle d-inline-flex align-items-center rounded border-0 collapsed" data-bs-toggle="collapse" data-bs-target="#home-collapse" aria-expanded="false">
                        <span className="material-symbols-outlined mx-1"> logout </span>
                        Cerrar Sesion
                    </button>
                </li>

            </ul>
        </div>
    </>
}
