import { React, useContext } from 'react';
import './producto.css'
import { useNavigate } from 'react-router-dom';
import { Context } from '../contextos/Contexto';
import axios from 'axios';

export function Producto({ productId = 0, q=2 }){

    const navigate = useNavigate();

    const { Artistas, Products, root } = useContext(Context);
    try { 
        const { fichaTecnica, precio, status, productImg } = Products.find(Prod => Prod._id === productId);
        const { titulo, autor } = fichaTecnica;

        function verProducto(){
            /*setProduct(productId);
            setView('Detalles');*/
            navigate(`/admin/obras/${productId}`);
        }

        function verArtista(){
            let Artista = Artistas.find( data => data.stageName === autor);

            /*setArtist(Artista.id);
            setView("Detalles del Artista");*/

            navigate(`/admin/artistas/${Artista._id}`);
        }

        async function borrarProducto(){
            var { data } = await axios.post(`${root}/borrarProduct`, { productId })
            console.log(data);
            return data;
        }

        return <div 
                    className='Producto' 
                    style={ { width:`${100 / q}%` } }
                >
                <img src={ productImg }
                        onClick = { verProducto }></img>
                <div className='Datos'>
                    <span>{ titulo }</span>
                    <span onClick={ verArtista }>{ autor }</span>
                    <span>${ precio }</span>
                    <span className='derecha'>{ status }</span>
                </div>
                <div>
                    <button onClick={borrarProducto}>
                        <span className="material-symbols-outlined">
                            delete
                        </span>
                    </button>
                </div>
        </div>
    } catch (err){
        //console.log(err);
        return <></>
    }

} 