import React, { useContext, useEffect, useState } from "react";

import axios from "axios";

import { Context } from "../contextos/Contexto";

import './mostrarImagenes.css'
import { Button } from "bootstrap";

export function MostrarImagenes(){
    const { root } = useContext(Context);
    const [ dataImagenes, setDataImagenes ] = useState([]);

    useEffect(() => {
        const traerImagenes = async () => {
            const imagenes = await axios.post(`${root}/verImagenes`);
            console.log(imagenes.data);
            setDataImagenes(imagenes.data.reverse());
        }
        traerImagenes();
    }, []);

    function handleClick(event){
        if(window.location.pathname.includes('subirImagen')) return true;

        let el = event.target;
        let classEl = el.attributes.class.value;
        let classParent = el.parentNode.attributes.class.value;
        
        if(classEl.includes('selected')){
            el.attributes.class.value = classEl.replace(` selected`, '');
            el.parentNode.attributes.class.value = classParent.replace(` bg-muted rounded`, '');
        } else {
            cancelarSeleccion();
            el.attributes.class.value = `${classEl} selected`;
            el.parentNode.attributes.class.value = `${classParent} bg-muted rounded`;
        }

        console.log( event.target.attributes.class.value);
        return true;
    }

    function cancelarSeleccion(){
        document.querySelectorAll('img').forEach((el, i) => {
            console.log(el.className);
            el.parentNode.className = el.parentNode.className.replace(' bg-muted rounded', '');
            el.className = el.className.replace(' selected', '');
        });
    }

    return <>
        {
            dataImagenes.map((dataImagen, i) => {
                let src = `${root}/assets/${dataImagen.filename}`;
                //console.log(src);
                return <div className="col-4" key={i}>
                    <div className="p-2">
                        <img className="serverImages w-100" src={src} onClick={handleClick}/>
                    </div>
                </div>
            })
        }
    </>
}