import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";
import { useState } from "react";
import { MostrarImagenes } from "../mostrarImagenes";

export function ObraImg(){

    const [funcion, setFuncion] = useState('Buscar Imagen');
    const [urlImagenes, setUrlImagenes] = useState([]);

    function listarSeleccion(){
        let srcSelect = [];

        document.querySelectorAll(`img.selected`).forEach(element => {
            //console.log(element.src);
            srcSelect.push(element.src);
        });

        console.log(srcSelect);

        return srcSelect;
    }

    
    function cancelarSeleccion(){
        document.querySelectorAll('img').forEach((el, i) => {
            console.log(el.className);
            el.parentNode.className = el.parentNode.className.replace(' bg-muted rounded', '');
            el.className = el.className.replace(' selected', '');
        });
    }
    
    function handleClickButton(){
        if(funcion == 'Buscar Imagen'){
            setFuncion('Seleccionar Imagenes');
            document.querySelector(`.seleccionarImagen`).style.display = 'block';
            return true;
        }
        
        setFuncion('Buscar Imagen');
        document.querySelector(`.seleccionarImagen`).style.display = 'none';
        setUrlImagenes(listarSeleccion());
        cancelarSeleccion();
        return true;
    }

    function seleccionarImagen(){
        let url = document.querySelector(`#urlObraImg`).value;
        url ? setUrlImagenes([url]) : console.log();
        return true;
        
    }


    return <>
        <Row className="mb-3">
            <Form.Label>Foto de la obra</Form.Label>
            {/*<InputGroup>                    
                <Form.Control id="fileObraImg" type="file"></Form.Control>
            </InputGroup>*/}
        </Row>

        <Row className="mb-3">
            <div className="input-group mb-3">
                <span id="enterObraImg" className="input-group-text material-symbols-outlined mx-1" onClick={seleccionarImagen}> login </span>
                <input id="urlObraImg" type="text" className="form-control" placeholder="Inserte aquí la url de la imagen" />
            </div>
        </Row>

        <Row>
            {
                urlImagenes.map((Url, i) => {
                    return <p className="urls" key={i}>{Url}</p>
                })
            }
        </Row>
        
        <Button variant='primary' className="mx-auto mb-2" style={ { display:'block' } } onClick={handleClickButton}> {funcion}</Button>

        <div className="seleccionarImagen rounded">
            <div className="row">
                <MostrarImagenes/>
            </div>
        </div>
    </>
}

export function obraImg(){
    /*const fileObraImg = document.querySelector(`#fileObraImg`).value;
    var urlObraImg = document.querySelector(`#urlObraImg`).value;

    if(fileObraImg) urlObraImg = fileObraImg;

    return urlObraImg;*/

    const urls = [];
    document.querySelectorAll('p.urls').forEach((p, i) => {
        urls.push(p.innerText);
        return true;
    });
    console.log(urls);

    return urls;
}

export function verObraImg(value){
    const _obraImg = obraImg;
    document.querySelector(`#urlObraImg`).value = value;
    document.querySelector(`#enterObraImg`).click();
    return _obraImg();
}