import React, { useContext } from "react";

import { Context } from "../contextos/Contexto";

export function Header(){
    const { Galeria, root } = useContext(Context);
    var galeryTitle = 'Nombre de la galería';
    Galeria[0] && Galeria[0].galeryName ? galeryTitle = Galeria[0].galeryName : galeryTitle = 'Nombre de la galeria';
    
    return <>
        <header className="navbar navbar-dark bg-dark py-2 shadow text-center">
            <div className="mx-auto">
                <a className="navbar-brand" href={root}>{galeryTitle}</a>
            </div>
        </header>
    </>
}