import axios from "axios";
import React, { useContext } from "react";
import { Form, Button } from "react-bootstrap";

import { Context } from "../../contextos/Contexto";

export function CrearAdmin(){
    const { root } = useContext(Context);
    
    function validarDatos(){
        var email = document.querySelector(`#email`).value;
        var password = document.querySelector(`#password`).value;
        var confirmPassword = document.querySelector(`#confirmPassword`).value;
        
        password === confirmPassword ? console.log('Exito') : password = false;
        let Usuario = { email, password, rol:'admin' };

        return Usuario;
    }

    async function crearAdmin(){
        const Usuario = validarDatos();
        if(Usuario.password === false) return alert('Las contraseñas no coinciden');
        
        let response = await axios.post(`${root}/crearUsuario`);
        console.log(response);

        window.location.reload();
    }

    return <>
        <Form className="
                border 
                border-primary 
                rounded-2 
                mx-auto
                my-3  
                py-3 
                px-3" 
                style={{ width: '350px'}}
            
            action={`${root}/signup`}
            method="post"
        >

            <div style={ { textAlign:"center", marginBottom:'15px' } }>
                <h2>
                    Crear cuenta de administrador
                </h2>
            </div>

            <Form.Group className='mb-3' controlId='formBasicEmail'>
                <Form.Label>Correo Electrónico</Form.Label>
                <Form.Control id="email" name="email" type='email' placeholder='Correo Electrónico' />
                <Form.Text className='text-muted'>
                    Esta información no será pública
                </Form.Text>
            </Form.Group>
            
            <Form.Group className='mb-3' controlId='formBasicEmail'>
                <Form.Label>Contraseña</Form.Label>
                
                <Form.Control id="password" name="password" type='password' placeholder='Contraseña' />

            </Form.Group>

            <Form.Group className='mb-3' controlId='formBasicEmail'>
                <Form.Label>Confirmar Contraseña</Form.Label>
                
                <Form.Control id="confirmPassword" type='password' placeholder='Contraseña' />

            </Form.Group>
            
            <Button type="submit" variant='primary' className="mx-auto" style={ { display:'block' } } > Crear Administrador </Button>
        </Form> 
    </>
}