import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function StageName({ placeholder='Nombre Artístico' }){
    return <>
        <Row className="mb-3">    
            <Form.Group as={Col}>
                <Form.Label>{placeholder}</Form.Label>
                <Form.Control id="stageName" type='text' placeholder={placeholder} />
            </Form.Group>
        </Row>
    </>
}

export function stageName(){
    const stageName = document.querySelector(`#stageName`).value;
    return stageName;
}

export function verStageName(value){
    document.querySelector(`#stageName`).value = value;
    return document.querySelector(`#stageName`).value;
}
