import React, { useContext, useEffect, useState } from "react";
import axios from "axios";

import { Context } from "../../contextos/Contexto";
import { Artista } from "../artista";
import { Producto } from "../producto";
import { VerProducto } from "../verProducto";

export function VerContactos(){
    const { root, Usuarios, Productos } = useContext(Context);
    var [contactos, setContactos ] = useState([]);

    useEffect(() => {
        async function traerContactos(){
            const response = await axios.post(`${root}/verCompradores`);
            console.log(response.data);
            setContactos(response.data.reverse());
            return response.data;
        }

        traerContactos();
    }, []);

    return <>
        {
            contactos.map((Cont, i) => {
                const { buyerType, location, obraCotizada, socialNetwork } = Cont;
                const { calle, numero, extension, cp, colonia, municipio, estado, pais  } = location;
                const { network, name, url } = socialNetwork;

                function direccion() {
                    if(!calle||!numero||!extension||!colonia||!cp||!municipio||!estado||!pais) return '';
                    else return `${calle} ${numero} ${extension}, ${colonia} ${cp}, ${municipio}, ${estado}, ${pais}`;
                }

                return <div key={i} className="mx-auto" style={{width:"90%"}} >
                    <div className="row my-2">
                        <div className="col-5">
                            <ul className="list-group">
                                <li className="list-group-item">
                                    <VerProducto productId={obraCotizada}/>
                                </li>
                            </ul>
                        </div>
                        <div className="col-7">
                            <ul className="list-group">
                                <li className="list-group-item">
                                        Tipo de comprador: <br />
                                        {buyerType}
                                </li>

                                <li className="list-group-item">
                                        Medio de contacto:<br />
                                        {network} : <a href={url}>{name}</a>
                                </li>

                                <li className="list-group-item">
                                        Direccion: <br />
                                        { direccion() }
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>


            })
        }
    </>;
}