import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";
import { RichTextEditor } from "./jodit";

export function HtmlDescription({title="Descripcion"}){
    return <>
        <Row className="my-3 p-3">
            <h4>{title}</h4>
            {/*<RichTextEditor id="htmlDescription"/>*/}
            <Form.Control id="htmlDescription" as={'textarea'} placeholder={`Lorem ipsum dolor sit amet...`}/>
        </Row>
    </>
}

export function htmlDescription(){
    const htmlDescription = document.querySelector(`#htmlDescription`).value;
    return htmlDescription;
}

export function verHtmlDescription(value){
    document.querySelector(`#htmlDescription`).value = value;
    return ;
    return document.querySelector(`#htmlDescription`).value;
}