import React, { useContext, useEffect } from "react";
import axios from "axios";

import { EditarArtistas } from "./formularios/editarArtistas";
import { EditarGalerias } from "./formularios/editarGalerias";
import { EditarObras } from "./formularios/editarObras";
import { Aside } from "./aside";
import { Context } from "../contextos/Contexto";
import { Artista } from "./artista";
import { Producto } from "./producto";
import { Login } from "./login";
import { useNavigate, useParams } from "react-router-dom";
import { CrearObra } from "./formularios/crearObra";
import { CrearArtista } from "./formularios/crearArtista";
import { CrearGaleria } from "./formularios/crearGaleria";
import { VerContactos } from "./formularios/verContactos";

export function Main( { view='artistas' } ){
    
    var { Artistas, Products, Galeria, Usuario, root } = useContext(Context);
    const navigate = useNavigate();
    const { id } = useParams();

    useEffect(() => {
        async function qGalUs(){
            let qUs = await (await axios.post(`${root}/countUsuarios`)).data;
            let qGal = await (await axios.post(`${root}/countGalerias`)).data;
            
            console.log(qUs);

            qUs < 1 || qGal < 1 ? navigate(`/admin/inicializarPagina`) : console.log(true);
    
            return true;
        }

        qGalUs();
    }, []);


    try{
        switch (view){
            case 'inicializarPagina':
                    return <>
                        <CrearGaleria />
                    </>;
                break;

            case 'artistas':
                return <main className="row">
                    <Aside />
                    <div className="col-9">
                        {
                            Artistas.map((Art, i) => {
                                return <Artista artistId={Art._id} q={2} key={i}/>
                            })
                        }
                    </div>
                </main>
                break;

            case 'obras':
                return <main className="row">
                    <Aside />
                    <div className="col-9">
                        {
                            Products.map((Prod, i) => {
                                return <Producto productId={Prod._id} key={i}/>
                            })
                        }
                    </div>
                </main>
                break;
            

            case 'crearObra':
                return <main className="row">
                    <Aside />
                    <div className="col-9">
                        <CrearObra />
                    </div>
                </main>
                break;
            
            case 'crearArtista':
                return <main className="row">
                    <Aside />
                    <div className="col-9">
                        <CrearArtista />
                    </div>
                </main>

            case 'editarArtistas':
                    var artista = Artistas.find((Art) => Art._id == id);
                    let workIds = [];
                    artista ? workIds = artista.workIds : workIds = [];
                    console.log(workIds);

                    return <main className="row">
                        <Aside />
                        <div className="col-9">
                            <EditarArtistas />
                            {
                                workIds.map((workId, i) => {
                                    return <Producto productId={workId} key={i}/>
                                })
                            }
                        </div>
                    </main>
                break;

            case 'editarGalerias':
                return <main className="row">
                    <Aside />
                    <div className="col-9">
                        <EditarGalerias />
                    </div>
                </main>
                break;

            case 'editarObras':
                return <main className="row">
                    <Aside />
                    <div className="col-9">
                        <EditarObras />
                    </div>
                </main>
                break;

            case 'verContactos':
                return <main className="row">
                    <Aside />
                    <div className="col-9">
                        <VerContactos />
                    </div>
                </main>
                break;

            case 'login':
                return <>
                    <Login></Login>
                </>;

            default:
                return <main className="row">
                    <Aside />
                    <div style={{ width:'70%', float:'right' }}>
                        asdasd
                    </div>
                </main>
        }
    } catch(err){
        console.log(err);
        return <></>;
    }


}