import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function Medidas(){
    return <>
        <h4>Medidas</h4>
        <Row className="mb-3">    
            <Form.Group as={Col}>
                <Form.Label>Ancho</Form.Label>
                <Form.Control id="ancho" type='number' placeholder='Ancho' />
            </Form.Group>
            <Form.Group as={Col}>
                <Form.Label>Alto</Form.Label>
                <Form.Control id="alto" type='number' placeholder='Ancho' />
            </Form.Group>
        </Row>
    </>
}

export function medidas() {
    const medidas = {
        alto:document.querySelector(`#alto`).value,
        ancho:document.querySelector(`#ancho`).value
    }

    return medidas;
}

export function verMedidas(value){
    const _medidas = medidas;

    document.querySelector(`#alto`).value = value.alto;
    document.querySelector(`#ancho`).value = value.ancho;
    
    return _medidas;
}