import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function Tecnica(){
    return <>
        <Row className="mb-3">    
            <Form.Group as={Col}>
                <Form.Label>Técnica</Form.Label>
                <Form.Control id="tecnica" type='text' placeholder='Técnica' />
            </Form.Group>
        </Row>
    </>
}

export function tecnica(){
    const tecnica = document.querySelector(`#tecnica`).value;
    return tecnica;
}

export function verTecnica(value){
    document.querySelector(`#tecnica`).value = value;
    return document.querySelector(`#tecnica`).value;
}