import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function Emails(){
    return <>
        <Form.Group as={Col}>
            <Form.Label>Correo Electronico</Form.Label>
            <Form.Control className="emails" type='email' placeholder='Correo Electronico' />
        </Form.Group> 
    </>
}

export function emails(){
    const emails = [];
    document.querySelectorAll(`.emails`).forEach((El, i) => { emails.push(El.value); });
    return emails;
}

export function verEmails(values){
    const _emails = emails;

    values.forEach((Val, i) => {
        document.querySelectorAll(`.emails`)[i].value = Val;
    });
    return _emails();
}