import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function Precio(){
    return <>
        <Form.Group as={Col}>
            <Form.Label>Precio</Form.Label>
            <Form.Control id="precio" type='number' placeholder='$0.00 MXN' />
        </Form.Group>
    </>
}

export function precio(){
    const precio = document.querySelector(`#precio`).value;
    return precio;
}

export function verPrecio(value){
    document.querySelector(`#precio`).value = value;
    return document.querySelector(`#precio`).value;
}