import React, { useContext, useEffect } from "react";

import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";
import { useNavigate, useParams } from "react-router-dom";

import { Context } from "../../contextos/Contexto";
//import { sellerName, stageName, locations, whatsApp, socialNetworks, emails, htmlDescription, workIds } from "./validadores";

import { SellerName, verSellerName, sellerName } from "./_sellerName";
import { StageName, verStageName, stageName } from "./_stageName";
import { ArtistImg, verArtistImg, artistImg } from "./_artistImg";
import { Locations, verLocations, locations } from "./_locations";
import { WhatsApp, verWhatsApp, whatsApp} from "./_whatsApp"
import { SocialNetworks, verSocialNetworks, socialNetworks } from "./_socialNetworks";
import { Emails, verEmails, emails } from "./_emails";
import  { HtmlDescription, verHtmlDescription, htmlDescription } from "./_htmlDescription";
//import { WorkIds } from "./";

import axios from "axios";

export function EditarArtistas(){

    const { Artistas, root } = useContext(Context);
    const { id } = useParams();
    const navigate = useNavigate();

    console.log('ID', id);

    useEffect(llenarDatos);

    function validarDatos(){
        const resultado = { 
            sellerName: sellerName(), 
            stageName: stageName(),
            artistImg: artistImg(),
            locations: locations(), 
            whatsApp: whatsApp(), 
            socialNetworks: socialNetworks(), 
            emails: emails(),
            htmlDescription: htmlDescription(),
            //workIds: workIds()
        }
        console.log(resultado);
        return resultado;
    }

    function llenarDatos(Artista = false){
        try{
            Artista = Artistas.find((Art) => Art._id == id);
            if(!Artista) return ; //El artista no existe o los datos no se han cargado
            const { sellerName, stageName, artistImg, locations, whatsApp, 
                    socialNetworks, emails, htmlDescription, workIds } = Artista;

            console.log(Artista);

            console.log(socialNetworks);

            verSellerName(sellerName);
            verStageName(stageName);
            verLocations(locations);
            verWhatsApp(whatsApp);
            verSocialNetworks(socialNetworks);
            verEmails(emails);
            verHtmlDescription(htmlDescription);
            verArtistImg(artistImg);
        } catch(err){
            console.log('Hubo un error');
        }
    }

    async function actualizarArtista(){
        let datos = validarDatos();
        let response = await axios.post(`${root}/actualizarArtista`, { datos, artistaId:id});
        console.log(response);

        window.location.reload();
        return response
    }

    function agregarObra(){
        navigate(`/admin/obras/crearObra/${id}`);
    }

    try{
        return <>
            <Form className="
                    border  
                    py-3 
                    px-3"
            >

                <h4>Artista</h4>
                    <SellerName />
                    <StageName />
                    <ArtistImg />
                    <Locations />
                    <Row className="mb-3">   
                        <Emails />
                        <WhatsApp />
                    </Row>
                    <SocialNetworks socialNetsArr={Artistas.find((Art) => Art._id == id).socialNetworks}/>
                    <HtmlDescription />

                
                <Button variant='primary' className="mx-auto" style={ { width:'30%', display:'block' } } onClick={ actualizarArtista }> Actualizar Artista </Button>
            </Form>

            <Form className="
                    border  
                    py-3 
                    px-3
                    mt-3"
            >

                <Button variant='primary' className="mx-auto" style={ { width:'30%', display:'block' } } onClick={ agregarObra }> Añadir Obra </Button>
            
            </Form>


        </>
    } catch (err) { console.log('error'); }
}