import { React, useContext } from "react";
import { useNavigate } from "react-router-dom";
import './artista.css';
import { Context } from "../contextos/Contexto";
import axios from "axios";

export function Artista({
        artistId = 0,
        q = 2,
    }){
    
    const navigate = useNavigate();

    const { Artistas, root } = useContext(Context);

    function verArtista(){
        /*setArtist(artistId);
        setView("Detalles del Artista");*/
        navigate(`/admin/artistas/${artistId}`);
    }

    async function borrarArtista(){
        var { data } = await axios.post(`${root}/borrarArtista`, { artistId })
        console.log(data);
        return data;
    }

    try {
        const { stageName, artistImg } = Artistas.find( Artist => Artist._id === artistId);
        const artistName = stageName;

        return <div className="Artista" 
                    style={ { width:`${100 / q}%` } }>
                        <div onClick = { verArtista }>
                            <img src={ artistImg }></img>
                            <div className="Datos">
                                <span>{ artistName }</span>
                                <span className="derecha">
                                    { '' }
                                </span>
                            </div>
                        </div>
                        <div>
                            <button onClick={borrarArtista}>
                                <span className="material-symbols-outlined">
                                    delete
                                </span>
                            </button>
                        </div>
        </div>
    } catch (err) {
        return <></>;
    }
}