import React, { useContext } from "react";
import { Form, Button } from "react-bootstrap";

import axios from "axios";
import { Context } from "../contextos/Contexto";

export function Login(){

    const { root } = useContext(Context);

    async function login(){
        var email = document.querySelector(`#email`).value;
        var password = document.querySelector(`#password`).value;
        
        let response = await axios.post(`${root}/signin`, { email, password });
        console.log(response.data);
        return true;
    }

    async function isAuth(){
        let response = await axios.post(`${root}/isAuth`);
        console.log(response.data);
        return true;
    }
    
    return <>
        <Form className="
                border 
                border-primary 
                rounded-2 
                mx-auto
                my-3  
                py-3 
                px-3" 
                style={{ width: '350px'}}
                action={`${root}/signin`}
                method="post"
        >

            <div style={ { textAlign:"center", marginBottom:'15px' } }>
                <h2>
                    Ingresar a mi cuenta
                </h2>
            </div>

            <Form.Group className='mb-3'>
                <Form.Label>Correo Electrónico</Form.Label>
                <Form.Control id="email" type='email' name="email" placeholder='Correo Electrónico' />
            </Form.Group>
            
            <Form.Group className='mb-3'>
                <Form.Label>Contraseña</Form.Label>
                <Form.Control id="password" type='password' name="password" placeholder='Contraseña' />
            </Form.Group>
            
            <Button type="submit" variant='primary' className="mx-auto" style={ { display:'block' } }> Iniciar Sesion </Button>

        </Form> 
    </>
}