import React from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function Titulo(){
    return <>
        <Row className="mb-3">    
            <Form.Group as={Col}>
                <Form.Label>Título de la obra</Form.Label>
                <Form.Control id="titulo" type='text' placeholder='Título de la obra' />
            </Form.Group>
        </Row>
    </>
}

export function titulo(){
    const titulo = document.querySelector(`#titulo`).value;
    return titulo;
}

export function verTitulo(value){
    document.querySelector(`#titulo`).value = value;
    return document.querySelector(`#titulo`).value;
}