import React from "react";
import { useState } from "react";

import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";

export function SocialNetwork({index = 0}){

    function handleChangeSelect(event){
        if(event.target.value !== 'otra'){
            document.querySelectorAll('.newNet')[index].style.display = 'none';
        } else {
           document.querySelectorAll('.newNetWork')[index].value = '';
            document.querySelectorAll('.newNet')[index].style.display = 'block';
        }
    }

    function eliminarRed(){
        document.querySelectorAll(`.network`)[index].className += ' hidden';
        document.querySelectorAll(`.socialNetwork`)[index].style.display = 'none';
    }

    return  <>
            <Row className="mb-3 socialNetwork">
                <hr />    
                <Form.Group as={Col}>
                    <Form.Label>Red Social</Form.Label>
                    <Form.Select className="network" placeholder='Social' onChange={handleChangeSelect}>
                        <option value={'facebook'}>Facebook</option>
                        <option value={'instagram'}>Instagram</option>
                        <option value={'twitter'}>Twitter</option>
                        <option value={'linkedin'}>LinkedIn</option>
                        <option value={'pinterest'}>Pinterest</option>
                        <option value={'otra'}>Otra</option>
                    </Form.Select>
                </Form.Group>

                <Form.Group as={Col}>
                    <Form.Label>Nombre de usuario</Form.Label>
                    <Form.Control className="name" type='text' placeholder='Nombre de usuario' />
                </Form.Group>

                <Form.Group>
                    <Form.Label>Enlace a tu perfil</Form.Label>
                    <Form.Control className="url" type='text' placeholder='Enlace a tu perfil' />
                </Form.Group>
                
                <Form.Group className="newNet" style={{display:'none'}}>
                    <Form.Label>Especifica el nombre de la red social</Form.Label>
                    <Form.Control className="newNetWork" type='text' placeholder='Red Social' />
                </Form.Group>

                <button type="button" className=" btn btn-danger mx-3 mt-3 float-right" style={{width:'10%'}} onClick={eliminarRed}>
                    <span className="material-symbols-outlined mx-1"> delete </span>
                </button>
            </Row>
        </>

}