import React, { useEffect } from "react";
import { useState } from "react";
import { Form, Button, Row, Col, InputGroup } from "react-bootstrap";
import { SocialNetwork } from "./_socialNetwork";

export function SocialNetworks({socialNetsArr = [{}]}){
    var [Forms, setForms] = useState(socialNetsArr);
    console.log(Forms);
    function agregarFormulario(){
        let last = Forms.length;
        setForms(Forms.concat([last]));
        console.log(Forms);
        return true;
    }

    return  <>
            <h4>Redes Sociales</h4>
                <button type="button" className=" btn btn-primary mb-2" style={{width:'10%'}} onClick={agregarFormulario}>
                    <span className="material-symbols-outlined mx-1"> add </span>
                </button>
                <button type="button" className=" btn btn-primary mb-2" style={{width:'10%'}} onClick={socialNetworks}>
                    <span className="material-symbols-outlined mx-1"> upload </span>
                </button>
                {
                    Forms.map((form, i) => {
                        return <div  key={i}> 
                            <SocialNetwork index={i}/>
                        </div>
                    })
                }
        </>

}

export function socialNetworks(){
    const socialNetworks = [];
    document.querySelectorAll(`.network`).forEach((El, i) => {
        if(El.className.includes('hidden')) return false;
        
        const socialNetwork = {};
                socialNetwork.network = El.value;
                socialNetwork.name = document.querySelectorAll(`.name`)[i].value;
                socialNetwork.url = document.querySelectorAll(`.url`)[i].value;

                socialNetwork.newNetwork = document.querySelectorAll(`.newNetWork`)[i].value;
            //VALIDAR QUE LOS DATOS SEAN CORRECTOS
                socialNetwork.network == 'otro' ? socialNetwork.network = socialNetwork.newNetwork : socialNetwork.network = socialNetwork.network;
        
        socialNetworks.push(socialNetwork);
    });
    console.log(socialNetworks);
    
    return socialNetworks;
}

export function verSocialNetworks(values){
    const _socialNetworks = socialNetworks;

    values.forEach((Val, i) => {
        let { network, name, url } = Val;
        document.querySelectorAll(`.network`)[i].value = network;
        document.querySelectorAll(`.name`)[i].value = name;
        document.querySelectorAll(`.url`)[i].value = url;
    });
    
    return _socialNetworks();
}